html {
  --header-color: #fff;
  --nav-link-color: #000;
  --nav-link-hover-color: #00acdc;
  font-family: Roboto, sans-serif;
}

* {
  margin: 0;
  padding: 0;
}

.logo {
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.header {
  background-color: var(--header-color);
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav_list {
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: var(--nav-link-color);
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__item :hover {
  color: var(--nav-link-hover-color);
}

.is-active:after {
  content: "";
  background-color: var(--nav-link-hover-color);
  border-radius: 8px;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}

.is-active {
  color: var(--nav-link-hover-color);
}
